<template>
  <div class="productManagement">
    <div class="header">
      <div class="productManagementHeader">
        <div class="headerContent">
          <van-icon
            name="arrow-left"
            @click="$router.push('/merchantCenter')"
          />
          <span class="title">{{ $t("index.text80") }}</span>

          <van-popover
            v-model="showPopover"
            theme="dark"
            trigger="click"
            :actions="actions"
            placement="bottom-end"
            @select="selectPopover"
          >
            <template #reference>
              <div class="right jcc">{{ $t("index.text43") }}</div>
            </template>
          </van-popover>
        </div>
      </div>
      <div class="slot"></div>
    </div>

    <div class="productManagementMain">
      <van-checkbox-group v-model="result">
        <div
          class="productInfoBox mt2r"
          v-for="item in productList"
          :key="item.id"
        >
          <img :src="item.full_url" alt="" class="productInfoBoxLeft" />
          <div class="productInfoBoxCenter">
            <div class="name">
              {{ item.title }}
            </div>
            <div class="price">{{ item.price }}HF</div>
          </div>
          <div class="productInfoBoxRight">
            <span v-if="item.state == 0">
              <i class="el-icon-edit-outline"></i>
              {{ $t("index.text104") }}
            </span>

            <div v-else>
              <span
                v-show="productId == 0"
                @click="
                  $router.push({
                    path: '/publishProducts',
                    query: {
                      productInfo: JSON.stringify({
                        productId: item.id,
                        status: false
                      })
                    }
                  })
                "
              >
                <i class="el-icon-edit-outline"></i>
                {{ $t("index.text105") }}
              </span>

              <!-- <span v-show="productId == 1" @click="offShelf(item.id)">
                <i class="el-icon-bottom"></i>
                {{ $t("index.text106") }}
              </span> -->
              <span v-show="productId == 2" @click="deleteProductId(item.id)">
                <i class="el-icon-delete"></i>
                {{ $t("index.text44") }}
              </span>
            </div>
            <!-- <van-checkbox
              checked-color="#e17544"
              :name="item"
              v-show="removeProduct == true"
            ></van-checkbox> -->
          </div>
        </div>
      </van-checkbox-group>
    </div>

    <!-- <div class="footer jcc" v-show="removeProduct == true">
      下架
    </div> -->
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      showPopover: false,
      result: [],
      productList: [],
      productId: 0
    };
  },
  computed: {
    actions() {
      return [
        { productId: 0, text: this.$t("index.text107"), className: "popover" },
        // { productId: 1, text: "下架商品", className: "popover" },
        { productId: 2, text: this.$t("index.text108"), className: "popover" }
      ];
    }
  },
  async activated() {
    this.getProductList();
  },
  methods: {
    // 获取商品列表
    getProductList() {
      this.$api
        .post("product/index", {
          page: 1,
          limit: 999999
        })
        .then(res => {
          this.productList = res.data.list;
        });
    },

    selectPopover(v) {
      this.productId = v.productId;
    },
    //  下架
    // offShelf(id) {
    //   this.$dialog
    //     .confirm({
    //       title: "下架商品"
    //     })
    //     .then(() => {
    //       this.$api
    //         .post("product/status", {
    //           id,
    //           status: 0
    //         })
    //         .then(res => {
    //           if (res.code == 200) {
    //             this.getProductList();
    //             this.$toast("下架成功");
    //           }
    //         });
    //     })
    //     .catch(() => {
    //       // on cancel
    //     });
    // },
    //  删除
    deleteProductId(id) {
      this.$dialog
        .confirm({
          title: this.$t("index.text109")
        })
        .then(() => {
          this.$api
            .post("product/del", {
              id
            })
            .then(res => {
              if (res.code == 200) {
                this.getProductList();
                this.$toast(this.$t("index.text110"));
              }
            });
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.productManagement {
  height: 100%;
  overflow: scroll;
  display: flex;
  flex-direction: column;
}

.productManagementHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  .title {
    color: #fff;
    font-size: 1.6rem;
  }
  .right {
    color: #fff;
  }
}

.productManagementMain {
  flex: 1;
  overflow-x: hidden;
  padding: 0 1.5rem 1.5rem;
  background: #fff;
  .productInfoBox {
    display: flex;
    .productInfoBoxLeft {
      width: 8.1rem;
      height: 8.1rem;
      border-radius: 0.4rem;
    }

    .productInfoBoxCenter {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-left: 0.8rem;

      .name {
        font-size: 1.2rem;
        font-weight: bold;
        line-height: 2.8rem;
      }

      .price {
        font-size: 1.6rem;
        color: #d12c21;
        font-weight: bold;
      }
    }

    .productInfoBoxRight {
      width: 7rem;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      text-align: center;
      .el-icon-edit-outline {
        font-size: 1.6rem;
        color: #999999;
      }
    }
  }
}

.footer {
  height: 5rem;
  background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
  box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
  color: #fff;
  font-size: 1.6rem;
}
</style>



